<template>
<main class="content container">
  <div class="content__top content__top--catalog">
    <h1 class="content__title">
      Каталог
    </h1>
    <span class="content__info">
      152 товара
    </span>
  </div>
  <div class="content__catalog">
    <ProductFilter v-model:price-from="filterPriceForm" v-model:price-to="filterPriceTo" v-model:category-id="filterCategoryId" v-model:color="filterColor" />
    <section class="catalog">
      <div v-if="productsLoading">Загрузка таваров...</div>
      <div v-if="productsLoadingFailed">Произошла ошибка <button @click.prevent="loadProducts">Попробовать еще раз</button></div>
      <ProductList :products="products" />
      <BasePagination v-model="page" :count="countProducts" :per-page="productsPerPage" />
    </section>

  </div>
</main>
</template>

<script>
import axios from "axios";

import ProductList from "@/components/ProductList.vue";
import BasePagination from "@/components/BasePagination.vue";
import ProductFilter from "@/components/ProductFilter.vue";
import {
    API_BASE_URL
} from "@/config";

export default {
    components: {
        ProductList,
        BasePagination,
        ProductFilter,
    },
    data() {
        return {
            filterPriceForm: 0,
            filterPriceTo: 0,
            filterCategoryId: 0,
            filterColor: 0,
            productsPerPage: 4,
            page: 1,
            productsData: null,
            productsLoading: false,
            productsLoadingFailed: false,
        };
    },
    computed: {
        products() {
            return this.productsData ?
                this.productsData.items.map((element) => ({
                    ...element,
                    image: element.image.file.url,
                })) :
                [];
        },
        countProducts() {
            return this.productsData ? this.productsData.pagination.total : 0;
        },
    },
    methods: {
        loadProducts() {
            this.productsLoading = true;
            this.productsLoadingFailed = false;
            clearTimeout(this.loadProductsTimer);
            this.loadProductsTimer = setTimeout(() => {
                axios
                    .get(`${API_BASE_URL}api/products`, {
                        params: {
                            page: this.page,
                            limit: this.productsPerPage,
                            categoryId: this.filterCategoryId,
                            minPrice: this.filterPriceForm,
                            maxPrice: this.filterPriceTo,
                        },
                    })
                // eslint-disable-next-line no-return-assign
                    .then((res) => this.productsData = res.data)
                // eslint-disable-next-line no-return-assign
                    .catch(() => this.productsLoadingFailed = true)
                // eslint-disable-next-line no-return-assign
                    .then(() => this.productsLoading = false);
            }, 0);
        },
    },
    watch: {
        page() {
            this.loadProducts();
        },
        filterPriceForm() {
            this.loadProducts();
        },
        filterPriceTo() {
            this.loadProducts();
        },
        filterCategoryId() {
            this.loadProducts();
        },
    },
    created() {
        this.loadProducts();
    },
};
</script>
