<!-- eslint-disable vue/no-deprecated-filter -->
<template>
  <li class="catalog__item">
    <router-link class="catalog__pic" :to="{name: 'product', params: {id: product.id}}">
      <img :src="product.image" :alt="product.title">
    </router-link>
    <h3 class="catalog__title">
      <a href="#">
        {{ product.title }}
      </a>
    </h3>
    <span class="catalog__price">
      {{ product.price | numberFormat }} ₽
    </span>
    <ul class="colors colors--black">
      <li class="colors__item" v-for="color of product.colors" :key="color">
        <label class="colors__label" :for="product.id + color">
          <input :id="product.id" class="colors__radio sr-only" type="radio" :name="product.id" :value="color">
          <span class="colors__value" :style="background(color)">
          </span>
        </label>
      </li>
    </ul>
  </li>
</template>
<script>

import goToPage from "@/helpers/goToPage";
import numberFormat from "@/helpers/numberFormat";

export default {
    data() {
        return {
            color: "#73B6EA",
        };
    },
    props: ["product"],
    filters: {
        numberFormat,
    },
    methods: {
        background(colvalue) {
            return {
                "background-color": colvalue,
            };
        },
        goToPage,
    },
};

</script>
