<template>
  <aside class="filter">
    <h2 class="filter__title">Фильтры</h2>
    <form class="filter__form form" action="#" method="get" @submit.prevent="submit">
      <fieldset class="form__block">
        <legend class="form__legend">Цена</legend>
        <label class="form__label form__label--price" for="start">
          <input id="start" class="form__input" type="text" name="min-price" v-model.number="currentPriceFrom">
          <span class="form__value">От</span>
        </label>
        <label class="form__label form__label--price" for="end">
          <input id="end" class="form__input" type="text" name="max-price" v-model.number="currentPriceTo">
          <span class="form__value">До</span>
        </label>
      </fieldset>
      <fieldset class="form__block">
        <legend class="form__legend">Категория</legend>
        <label class="form__label form__label--select" for="cat">
          <select class="form__select" type="text" name="category" id="cat" v-model.number="currentCategoryId">
            <option value="0">Все категории</option>
            <option v-for="category of categories" :key="category.id" :value="category.id" >{{ category.title }}</option>
          </select>
        </label>
      </fieldset>
      <fieldset class="form__block">
        <legend class="form__legend">Цвет</legend>
        <ul class="colors">
          <li class="colors__item" v-for="color of colors" :key="color.id">
            <label class="colors__label" :for="color.id">
              <input :id="color.id" class="colors__radio sr-only" type="radio" name="color" :value="color.value" v-model="currentColor" @click="clicked(color.value)">
              <span class="colors__value" :style="background(color.value)">
              </span>
            </label>
          </li>
        </ul>
      </fieldset>
      <button class="filter__submit button button--primery" type="submit">
        Применить
      </button>
      <button class="filter__reset button button--second" type="button" @click.prevent="reset">
        Сбросить
      </button>
    </form>
  </aside>
</template>

<script>
import axios from "axios";

import colors from "@/data/colors";
import { API_BASE_URL } from "@/config";

export default {
    data() {
        return {
            currentPriceFrom: 0,
            currentPriceTo: 0,
            currentCategoryId: 0,
            currentColor: 0,
            categoriesData: null,
        };
    },
    props: ["priceFrom", "priceTo", "categoryId", "color"],
    computed: {
        categories() {
            return this.categoriesData ? this.categoriesData.items : [];
        },
        colors() {
            return colors;
        },
    },
    watch: {
        priceFrom(value) {
            this.currentPriceFrom = value;
        },
        priceTo(value) {
            this.currentPriceTo = value;
        },
        categoryId(value) {
            this.currentCategoryId = value;
        },
        color(value) {
            this.currentColor = value;
        },
    },
    methods: {
        submit() {
            this.$emit("update:priceFrom", this.currentPriceFrom);
            this.$emit("update:priceTo", this.currentPriceTo);
            this.$emit("update:categoryId", this.currentCategoryId);
            this.$emit("update:color", this.currentColor);
        },
        reset() {
            this.$emit("update:priceFrom", 0);
            this.$emit("update:priceTo", 0);
            this.$emit("update:categoryId", 0);
            this.$emit("update:color", 0);
        },
        clicked(val) {
            this.currentColor = val;
        },
        background(colvalue) {
            return {
                "background-color": colvalue,
            };
        },
        loadCategories() {
            axios.get(`${API_BASE_URL}api/productCategories`)
                .then((res) => { this.categoriesData = res.data; });
        },
    },
    created() {
        this.loadCategories();
    },
};
</script>
