<!-- eslint-disable vue/no-deprecated-filter -->
<template>
<div>
  <main class="content container" v-if="productLoading">Загрузка товара...</main>
  <main class="content container" v-else-if="!productData">Произошла ошибка</main>
  <main class="content container" v-if="productData">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            {{ category.title }}
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            {{ product.title }}
          </a>
        </li>
      </ul>
    </div>
    <section class="item">
      <div class="item__pics pics">
        <div class="pics__wrapper">
          <img width="570" height="570" :src="product.image" :alt="product.title">
        </div>
      </div>
      <div class="item__info">
        <span class="item__code">Артикул: {{ product.id }}</span>
        <h2 class="item__title">
          {{ product.title }}
        </h2>
        <div class="item__form">
          <form class="form" action="#" method="POST" @submit.prevent="addToCart">
            <b class="item__price">
              {{ product.price | numberFormat }} ₽
            </b>
            <fieldset class="form__block">
              <legend class="form__legend">Объемб в ГБ:</legend>
              <ul class="sizes sizes--primery">
                <li class="sizes__item">

                </li>
              </ul>
            </fieldset>
            <div class="item__row">
              <div class="form__counter">
                <button type="button" aria-label="Убрать один товар" @click.prevent="minusAmount()">
                  <svg width="12" height="12" fill="currentColor">
                    <use xlink:href="#icon-minus"></use>
                  </svg>
                </button>
                <label for="amount">
                  <input id="amount" type="text" value="1" v-model.number="productAmount">
                </label>
                <button type="button" aria-label="Добавить один товар" @click.prevent="plusAmount()">
                  <svg width="12" height="12" fill="currentColor">
                    <use xlink:href="#icon-plus"></use>
                  </svg>
                </button>
              </div>
              <button class="button button--primery" type="submit" :disabled="productAddSending">
                В корзину
              </button>
              <div v-show="productAdding">Товар добавлен в корзину</div>
              <div v-show="productAddSending">Добавляем товар в корзину</div>
            </div>
          </form>
        </div>
      </div>
    </section>
  </main>
</div>
</template>

<script>
import axios from "axios";
import { mapActions } from "vuex";

import {
    API_BASE_URL,
} from "@/config";
import goToPage from "@/helpers/goToPage";
import numberFormat from "@/helpers/numberFormat";

export default {
    data() {
        return {
            productAmount: 1,
            productData: null,
            productLoading: false,
            productLoadingFailed: false,
            productAdding: false,
            productAddSending: false,
        };
    },
    filters: {
        numberFormat,
    },
    computed: {
        product() {
            return this.productData;
        },
        category() {
            return this.productData.category;
        },
    },
    methods: {
        ...mapActions(["addProductToCart"]),
        goToPage,
        addToCart() {
            this.productAdding = false;
            this.productAddSending = true;
            this.addProductToCart({
                productId: this.product.id,
                amount: this.productAmount,
            })
                .then(() => {
                    this.productAdding = true;
                    this.productAddSending = false;
                });
        },
        loadProduct() {
            this.productLoading = true;
            this.productLoadingFailed = false;
            axios.get(`${API_BASE_URL}api/products/${this.$route.params.id}`)
            // eslint-disable-next-line no-return-assign
                .then((res) => {
                    this.productData = {
                        ...res.data,
                        image: res.data.image.file.url,
                    };
                })
            // eslint-disable-next-line no-return-assign
                .catch(() => this.productLoadingFailed = false)
            // eslint-disable-next-line no-return-assign
                .then(() => this.productLoading = false);
        },
        minusAmount() {
            if (this.productAmount > 1) {
                this.productAmount -= 1;
            }
        },
        plusAmount() {
            this.productAmount += 1;
        },
    },
    watch: {
        "this.$route.params.id": {
            handler() {
                this.loadProduct();
            },
            immediate: true,
        },
    },
};
</script>
