import BaseFromField from "@/components/BaseFromField.vue";
export default {
    name: "BaseFromTextarea",
    props: ["title", "error", "placeholder", "value"],
    components: {
        BaseFromField
    },
    computed: {
        dataValue: {
            get(){
                return this.value;
            },
            set(value) {
                this.$emit("input", value);
            }
        }
    }
};